.sidebar-toggle {
  width: 345px;
  position: fixed;
  height: 100%;
  background: #dd2f6e;
  background: var(--theme--main-color);
  top: 0;
  left: 0;
  transition: 0.3s;
}
.sidebar-brand {
  color: black;
  height: 90px;
  margin: 1em auto;
}
.sidebar-brand span {
  color: aliceblue;
  display: inline-block;
  margin-right: 1rem;
}
.main-content {
  margin-left: 345px;
  transition: 0.3s;
}
.sidebar-menu {
  margin-left: 0.5em;
}
.sidebar-menu a {
  padding-left: 1rem;
  color: white;
  display: block;
  font-size: 1.1rem;
}
.sidebar-menu a.active,
.sidebar-menu a:hover {
  background: white;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  color: #dd2f6e;
  color: var(--theme--main-color);
  border-radius: 30px 0 0 30px;
  text-decoration: none;
  transition: 0.2s;
}
.sidebar-menu li {
  width: 100%;
