.main-content-head {
  display: flex;
  justify-content: space-between;
  padding: 0.4em;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
  position: fixed;
  width: calc(100vw - 355px);
  top: 0;
  left: 345px;
  z-index: 100;
  transition: 0.3s;
}
#nav-toggle:checked + .sidebar-toggle {
  width: 65px;
}
#nav-toggle:checked + .sidebar-toggle .sidebar-brand span:last-child,
#nav-toggle:checked + .sidebar-toggle li a span:last-child {
  display: none;
  transition: 0.3s;
}
#nav-toggle:checked + .sidebar-toggle li {
  text-align: center;
  margin-left: 0em;
}
#nav-toggle:checked + .sidebar-toggle li a {
  padding-left: 0.3em;
}
#nav-toggle:checked ~ .main-content {
  margin-left: 65px;
}
#nav-toggle:checked ~ .main-content .main-content-head {
  left: 65px;
  width: calc(100vw - 75px);
}
