@media only screen and (max-width: 1200px) {
  .sidebar-toggle {
    width: 65px;
  }
  .sidebar-toggle .sidebar-brand span:last-child,
  .sidebar-toggle li a span:last-child {
    display: none;
    transition: 0.3s;
  }
  .sidebar-toggle li {
    text-align: center;
    margin-left: 0em;
  }
  .sidebar-toggle li a {
    padding-left: 0.3em;
  }
  .main-content {
    margin-left: 65px;
  }
  .main-content .main-content-head {
    left: 65px;
    width: calc(100vw - 75px);
  }
  .sidebar-toggle:hover {
    width: 345px;
    z-index: 200;
  }
  .sidebar-toggle:hover .sidebar-brand span:last-child,
  .sidebar-toggle:hover li a span:last-child {
    display: inline-block;
    transition: 0.3s;
  }
  .sidebar-toggle li {
    text-align: left;
    margin-left: 0em;
  }
  .sidebar-toggle:hover li a {
    padding-left: 2em;
  }
  #nav-toggle:checked + .sidebar-toggle .sidebar-brand span:last-child,
  #nav-toggle:checked + .sidebar-toggle li a span:last-child {
    display: inline;
  }
  #nav-toggle:checked + .sidebar-toggle {
    width: 345px;
    z-index: 200;
  }
  #nav-toggle:checked ~ .main-content .main-content-head {
    margin-left: 280px;
    z-index: 300;
    width: calc(100vw - 355px);
  }
  #nav-toggle:checked + .sidebar-toggle li {
    text-align: left;
    margin-left: 0em;
  }
}
@media only screen and (max-width: 960px) {
  .cards {
    grid-template-columns: 1fr 1fr;
  }
  .recent-grid {
    grid-template-columns: 55% auto;
  }
}
@media only screen and (max-width: 768px) {
  .cards {
    grid-template-columns: 100%;
  }
  .recent-grid {
    grid-template-columns: 100%;
  }
  .customers {
    margin-left: 0;
    margin-top: 2em;
  }
}

